@import '../../assets/css/var.scss';
@import '../../assets/css/mixins';

.new_home {
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
  width: 100%;
  @include webpSupport {
    background-image: url('../../assets/images/home2/base/top_bg.webp');
  }
  @include webpUnSupport {
    background-image: url('../../assets/images/home2/base/top_bg.png');
  }
  .sunTokenWrapper {
    margin-top: 149px;
    width: 100%;
  }

  .developCardWrapper {
    margin-top: 70px;
  }

  .dayStatusWrapper {
    margin: 100px auto 0;
    width: 1440px;
  }

  .defiCardWrapper {
    margin: 50px auto 0;
    //width: 1920px;
    position: relative;
  }

  .noticeWrapper {
    margin-bottom: -210px;
  }

  .partnerWrapper {
  }

  .poolsWrapper {
    margin: 0 auto;
    background-image: url('../../assets/images/home2/pools/bg1.svg');
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    padding-top: 80px;
    position: relative;
    //top: -60px;
    z-index: 5;
    width: 100%;
  }

  .fullWrapper {
    width: 100%;
    overflow: hidden;
    margin-top: -60px;
  }
}

@include pc-small-screen {
  .new_home {
    .dayStatusWrapper {
      width: 1200px;
    }
    .noticeWrapper {
      margin-bottom: -240px;
    }
  }
}

@include mobile-only {
  // .partnerWrapper {
  //   margin-top: -17px;
  // }
  .new_home {
    background-image: url(../../assets/images/home2/base/top_bg_m.svg) !important;
    background-position: top 50px left;
    background-size: 100% auto;

    .topSwapWrapper {
      margin: 16px auto 0;
      padding: 0 10px;
      width: 100%;
    }
    .dayStatusWrapper {
      margin: 30px auto 0;
      padding: 0 15px;
      width: 100%;
    }

    .defiCardWrapper {
      margin: 60px auto 0;
      padding: 0 15px;
      width: 100%;
      background: url('../../assets/images/home2/defiCard/bg_mobile.png') no-repeat top center / contain;
    }
    .sunTokenWrapper {
      margin-top: 60px;
    }

    .noticeWrapper {
      margin-bottom: -17px;
    }
    .partnerWrapper {
    }
    .poolsWrapper {
      margin: 0 auto;
      padding: 60px 15px 0;
      width: 100%;
      background-image: url('../../assets/images/home2/pools/bg_m.png');
      background-size: contain;
      background-position: center 20px;
      position: static;
    }
    .fullWrapper {
      margin-top: 0;
    }
  }
}

.topSwapWrapper {
  margin: 60px auto 0;
  width: 1440px;
  @include pc-small-screen {
    width: 1200px;
  }
}
