.ani-arrow {
  position: relative;
  // padding-right: 18px;
  padding-right: 20px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  cursor: pointer;

  .icon {
    z-index: 3;
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    width: 16px;
    height: 16px;
    transform: translateY(-50%) rotate(-45deg);
    transition: all 0.3s linear;
    // border: 1px solid #f00;
  }

  &:hover {
    .icon {
      animation: roatedSimple 0.3s linear forwards;
    }
  }

  // 
  &:not(:hover):not(.ani-arrow-active) .icon {
    animation: roatedSimpleReverse 0.3s linear forwards;
  }

  &.ani-arrow-active {
    .icon {
      animation: roatedSimple 0.3s linear forwards;
    }
  }
}

.ani-arrow-right {
  padding-right: 18px;
  .icon {
    width: 12px;
    height: 12px;
    transform: translateY(-50%) rotate(0);
  }

  &:hover {
    .icon {
      animation: arrowRight 0.3s linear forwards;
    }
  }

  // 
  &:not(:hover):not(.ani-arrow-active) .icon {
    animation: arrowRightReverse 0.3s linear forwards;
  }

  &.ani-arrow-active {
    .icon {
      animation: arrowRight 0.3s linear forwards;
    }
  }
}

@keyframes arrowRight {
  0% {
    transform: translateX(0) translateY(-50%);
  }
  to {
    transform: translateX(4px) translateY(-50%);
  }
}

@keyframes arrowRightReverse {
  0% {
    transform: translateX(4px) translateY(-50%);
  }
  to {
    transform: translateX(0) translateY(-50%);
  }
}

@keyframes roatedSimple {
  0% {
    transform: translateY(-50%) translateX(0) rotate(-45deg);
  }
  to {
    transform: translateY(-50%) translateX(2px) rotate(0);
  }
}

@keyframes roatedSimpleReverse {
  0% {
    transform: translateY(-50%) translateX(2px) rotate(0);
  }
  to {
    transform: translateY(-50%) translateX(0) rotate(-45deg);
  }
}
