@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.btn {

  cursor: pointer;
  transition: all 0.3s;
  text-align: center;
  -webkit-tap-highlight-color: transparent;
  width: 126px;
  height: 32px;
  border-radius: 32px;
  border: 1px solid #fff;
  line-height: 30px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 12px;
  color: #fff;

  &:hover,
  &:active {
    background: #6400FF33;
  }
}

@include mobile-only {
  .btn {
    height: 36px;
    line-height: 36px;
    font-size: 16px;
  }
}
