@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -46px;
        top: 0;
        width: 26px;
        height: 26px;
        background: url(../../../assets/images/home/base/ico_defiCard_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.defi_content {
  position: absolute;
  top: 162px;
  left: 50%;
  width: 1440px;
  margin-left: -720px;
  z-index: 1;
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  color: rgba(255, 255, 255);
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.defiCard {
  .list {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2  */
    grid-template-rows: repeat(2, auto); /* 2  */
    gap: 10px;
    margin-top: 50px;
    height: 530px;
    .item {
      position: relative;
      height: 256px;
      display: flex;
      align-items: center;
      .item_main {
        padding: 0;
      }
      .item_card {
        position: relative;
        height: 170px;
        width: 270px;
        flex-shrink: 0;
        margin: 0 20px;
      }
      .item_title {
        font-family: Poppins;
        font-weight: 600;
        font-size: 22px;
        line-height: 33px;
        color: #fff;
        //margin-top: 10px;
        span {
          opacity: 0;
        }
      }
      .item_tags_list {
        opacity: 0;
        display: inline-flex;
        margin-top: 17px;
        .item_tag {
          margin-right: 10px;
          padding: 3px 6px;
          font-family: Poppins;
          font-weight: 400;
          font-size: 12px;
          line-height: 18px;
          border-radius: 4px;
          background: rgb(#7d71d9, 0.15);
          color: #9f95cb;
          &:last-child {
            margin-right: 0;
          }
        }
      }
      .item_desc {
        opacity: 0;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        margin-top: 20px;
        //height: 66px;
        line-height: 22px;
        color: #a29cbc;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 370px;
        @include mobile-only {
          width: auto !important;
        }
      }
      &:last-child .item_desc {
        width: 364px;
      }
      &:nth-child(2) .item_desc {
        width: 360px;
      }
      .btn {
        opacity: 0;
        margin-top: 30px;
      }
      &:nth-child(1) {
        .item_card_icon1 {
          z-index: 1;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home2/defiCard/g1_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 2;
          position: absolute;
          left: 20px;
          top: 75px;
          width: 55px;
          height: 55px;
          background-image: url('../../../assets/images/home2/defiCard/g1_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 3;
          position: absolute;
          right: 20px;
          top: 16px;
          width: 57px;
          height: 57px;
          background-image: url('../../../assets/images/home2/defiCard/g1_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          z-index: 4;
          position: absolute;
          right: 30px;
          bottom: 18px;
          width: 53px;
          height: 53px;
          background-image: url('../../../assets/images/home2/defiCard/g1_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          .item_card_icon2 {
            //left: 26px;
            left: 32px;
          }
          .item_card_icon3 {
            //right: 40px;
            right: 32px;
          }
          .item_card_icon4 {
            //right: 1px;
            right: 18px;
          }
        }
      }
      &:nth-child(2) {
        .item_card_icon1 {
          z-index: 2;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home2/defiCard/g2_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 4;
          position: absolute;
          left: 76px;
          top: 30px;
          width: 110px;
          height: 90px;
          background-image: url('../../../assets/images/home2/defiCard/g2_x.png');
          background-size: cover;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }

        .item_card_icon3 {
          display: none;
          //z-index: 3;
          //position: absolute;
          //left: 100px;
          //top: 25px;
          //width: 70px;
          //height: 70px;
          //background-image: url('../../../assets/images/home/defiCard/g2_3.png');
          //background-size: 100% 100%;
          //background-repeat: no-repeat;
          //transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          display: none;
          //z-index: 5;
          //position: absolute;
          //right: 72px;
          //top: 49px;
          //width: 70px;
          //height: 70px;
          //background-image: url('../../../assets/images/home/defiCard/g2_4.png');
          //background-size: 100% 100%;
          //background-repeat: no-repeat;
          //transition: all 0.3s ease-in-out;
        }
        .item_card_icon5 {
          z-index: 1;
          position: absolute;
          right: 20px;
          top: 32px;
          width: 80px;
          height: 80px;
          background-image: url('../../../assets/images/home2/defiCard/g2_5.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          .item_card_icon2 {
            top: 25px;
            transform: scale(1.09);
          }
          //.item_card_icon3 {
          //  top: 15px;
          //  transform: scale(1.1);
          //}
          //.item_card_icon4 {
          //  top: 39px;
          //  transform: scale(1.1);
          //}
          .item_card_icon5 {
            right: 20px;
            top: 22px;
            transform: scale(1.2);
          }
        }
      }
      &:nth-child(3) {
        .item_card_icon1 {
          z-index: 3;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home2/defiCard/g3_1.png');
          background-size: cover;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 2;
          position: absolute;
          left: 30px;
          top: 40px;
          width: 74px;
          height: 74px;
          background-image: url('../../../assets/images/home2/defiCard/g3_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 4;
          position: absolute;
          left: 92px;
          top: 3px;
          width: 110px;
          height: 110px;
          background-image: url('../../../assets/images/home2/defiCard/g3_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          display: none;
          z-index: 1;
          position: absolute;
          right: 5px;
          top: 20px;
          width: 120px;
          height: 130px;
          background-image: url('../../../assets/images/home/defiCard/g3_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }

        &:hover {
          .item_card_icon2 {
            transform: rotateZ(20deg);
            transform-origin: center;
          }
          .item_card_icon3 {
            transform: rotateZ(-15deg) scale(1.13);
          }
          //.item_card_icon4 {
          //  right: 0px;
          //  top: 15px;
          //  transform: scale(1.2);
          //}
        }
      }
      &:nth-child(4) {
        .item_card_icon1 {
          z-index: 2;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home2/defiCard/g4_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 3;
          position: absolute;
          left: 81px;
          bottom: 28px;
          width: 22px;
          height: 22px;
          background-image: url('../../../assets/images/home2/defiCard/g4_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 4;
          position: absolute;
          left: 96px;
          top: 20px;
          width: 90px;
          height: 90px;
          background-image: url('../../../assets/images/home2/defiCard/g4_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          z-index: 1;
          position: absolute;
          right: 28px;
          top: 61px;
          width: 102px;
          height: 95px;
          background-image: url('../../../assets/images/home2/defiCard/g4_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          .item_card_icon2 {
            left: 75px;
            bottom: 33px;
            width: 16px;
            height: 16px;
          }
          .item_card_icon3 {
            transform: scale(1.18);
            top: 8px;
          }
          .item_card_icon4 {
            right: 18px;
            top: 53px;
            transform: scale(0.84);
          }
        }
      }
    }
    .m_img {
      display: none;
    }
  }
}

.defiCardActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.defiCard {
  .listActive {
    .item {
      .item_title {
        span {
          display: inline-block;
          animation: homeFadeInUp 0.6s ease-in-out forwards;
        }
      }
      .item_tags_list {
        animation: homeFadeInUp 0.6s ease-in-out 0.4s forwards;
      }
      .item_desc {
        animation: homeFadeInUp 0.6s ease-in-out 1s forwards;
      }
      .btn {
        animation: homeFadeIn 0.6s ease-in-out 1.6s forwards;
      }
    }
  }
}

// .dayStatusInview {
//   animation: homeFadeIn 1s ease-in-out forwards;
// }
@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    width: 258px;
    margin: 0 auto;
    span {
      &:first-child {
        &::before {
          left: -26px;
          top: -13px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin: 10px auto 0;
    width: 325px;
    font-size: 14px;
    line-height: 21px;
    color: #fff;
  }

  .defiCard {
    .list {
      grid-template-columns: 1fr;
      margin-top: 20px;
      height: auto;
      .m_img {
        display: block;
        img {
          display: block;
          width: 100%;
        }
      }
      .item {
        position: relative;
        background: rgba(10, 1, 24, 0.75);
        -webkit-backdrop-filter: blur(20px);
        backdrop-filter: blur(20px);
        padding: 20px;
        height: initial;
        border-radius: 20px;
        flex-direction: column;
        &::before {
          content: '';
          position: absolute;
          inset: 0;
          border-radius: inherit; /*  */
          padding: 1px; /*  */
          background: linear-gradient(180deg, rgba(110, 13, 255, 0.6) 0%, rgba(110, 13, 255, 0) 100%);
          -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
          -webkit-mask-composite: xor;
          mask-composite: exclude;
          pointer-events: none; /*  */
        }
        //&:hover {
        //  background: linear-gradient(122.68deg, rgba(19, 6, 46, 0.5) 14.19%, rgba(26, 14, 44, 0.5) 85.09%);
        //}
        .item_main {
          margin-top: 7px;
          width: 100%;
        }
        .item_card {
          height: initial;
          margin: 0 auto;
        }
        .item_top {
          display: flex;
          align-items: center;
        }
        .item_title {
          font-size: 20px;
          line-height: 30px;
          margin-top: 0;
          margin-right: 12px;
        }
        .item_tags_list {
          margin-top: 0;
          .item_tag {
            padding: 0 6px;
            margin-right: 10px;
            line-height: 24px;
            &:last-child {
              margin-right: 0;
            }
          }
        }
        .item_desc {
          margin-top: 19px;
          height: 44px;
          -webkit-line-clamp: 2;
        }
        .btn {
          margin-top: 26px;
          .inner_btn {
            width: 100%;
            height: 34px;
            line-height: 32px;
            font-size: 14px;
            box-sizing: border-box;
          }
        }
        .item_card_icon1,
        .item_card_icon2,
        .item_card_icon3,
        .item_card_icon4,
        .item_card_icon5 {
          display: none;
        }
      }
    }
    .listActive {
      .item {
        .item_title {
          span {
            animation: homeFadeInUp 0.8s ease-in-out forwards;
          }
        }
        .item_tags_list {
          animation: homeFadeInUp 0.8s ease-in-out 0.6s forwards;
        }
        .item_desc {
          animation: homeFadeInUp 0.8s ease-in-out 1.2s forwards;
        }
        .btn {
          animation: homeFadeInUp 0.8s ease-in-out 1.8s forwards;
        }
      }
    }
  }
  .defi_content {
    position: static;
    width: 100%;
    margin: 0 auto;
  }
}
.bg_video_wrap {
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.bg_video {
  width: 1920px;
  height: 990px;
  margin: 0 auto;
  &_small {
    display: none;
  }
}
@include pc-small-screen {
  .bg_video {
    display: none;
    &_small {
      display: block;
      width: 1512px;
      height: auto;
    }
  }
  .defiCard {
    .list .item .item_desc {
      width: 275px;
    }
    .list .item {
      &:nth-child(2) .item_desc {
        width: 251px;
      }
      &:nth-child(4) .item_desc {
        width: 206px;
      }
      .item_card {
        margin: 0 10px 0 20px;
      }
    }
  }
  .defi_content {
    width: 1200px;
    margin-left: -600px;
  }
}
