@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.header {
  z-index: 3;
  position: relative;
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
  text-align: center;
  color: #fff;
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.sunToken {
  position: relative;
  &::before {
    inset: 0;
    z-index: 2;
    position: absolute;
    left: 50%;
    top: -262px;
    content: '';
    width: 1920px;
    height: 980px;
    transform: translateX(-50%);
    background: url(../../../assets/images/home2/sunToken/box_bg_1.png) no-repeat;
    background-size: 100% 100%;
    pointer-events: none;
  }
  &::after {
    inset: 0;
    position: absolute;
    left: 0;
    top: 12px;
    content: '';
    width: 516px;
    height: 1400px;
    background: url(../../../assets/images/home2/sunToken/box_bg_2.png) no-repeat;
    background-size: 100% 100%;
    pointer-events: none;
  }
  width: 100%;
  // overflow: hidden;
  .sunTokenInner {
    position: relative;
    margin: 0 auto;
    // width: 1920px;
    width: 100%;
  }
  .box {
    position: relative;
    margin: 0 auto;
    width: 1440px;
  }
}

.data {
  margin-top: 81px;
  .market {
    position: relative;
    .market_title {
      position: absolute;
      top: -10px;
      right: 10px;
      transform: translateY(-100%);
      display: flex;
      align-items: center;
      span {
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: rgb(180, 171, 219, 0.7);
      }
      .market_refresh {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #271b4a;
        cursor: pointer;
        span {
          margin-right: 6px;
        }
        &:hover {
          background-color: #382868;
          img {
            transform: rotate(-360deg);
          }
        }
        img {
          display: block;
          width: 8px;
          height: 8px;
          transition: all 0.3s;
        }
      }
      .market_refresh_disabled {
        cursor: not-allowed;
        background: rgba(48, 34, 68, 1);
        &:hover {
          background: rgba(48, 34, 68, 1);
          img {
            transform: rotate(0);
          }
        }
      }
    }

    .market_list {
      z-index: 3;
      position: relative;
      display: flex;
      padding: 0 40px;
      background: rgba(10, 1, 24, 0.3);
      border-radius: 20px;
      backdrop-filter: blur(20px);
      &::before {
        content: '';
        position: absolute;
        inset: 0;
        border-radius: 20px;
        padding: 1px;
        background: linear-gradient(180deg, rgba(167, 162, 255, 0.24) 0%, rgba(110, 13, 255, 0) 100%);
        -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
        -webkit-mask-composite: xor; /* Safari/Chrome */
        mask-composite: exclude; /* Firefox */
        pointer-events: none;
      }

      .market_item {
        opacity: 0;
        flex: 1;
        position: relative;
        padding: 35px 30px;
        margin-right: 125px;
        height: 140px;
        &:last-child {
          margin-right: 0;
        }
        .item_name {
          display: flex;
          align-items: center;
          span {
            font-family: Poppins;
            font-weight: 500;
            font-size: 16px;
            line-height: 21px;
            margin-right: 10px;
            color: rgba(180, 171, 219, 1);
          }
          .item_rate {
            display: block;
          }
        }
        .item_rate {
          display: none;
          font-family: Poppins;
          font-weight: 400;
          font-style: Regular;
          font-size: 14px;
          line-height: 21px;
          color: #00ebb4;
        }
        .item_rate_down {
          color: #ff3287;
        }
        .item_num {
          margin-top: 10px;
          font-family: Poppins-Bold;
          font-weight: 700;
          font-size: 28px;
          line-height: 42px;
          color: #fff;
        }
        .item_chart {
          position: absolute;
          right: 30px;
          top: 63px;
          width: 130px;
          height: 40px;
        }
        &:first-child {
          margin-right: 105px;
          .item_chart {
            right: 80px;
          }
        }
      }
    }
  }
  .burn {
    z-index: 3;
    position: relative;
    padding: 70px 0 0 70px;
    min-height: 764px;
    .burn_line0 {
      display: inline-block;
      margin: 30px 0 30px;
      // width: 480px;
      width: 381px;
      height: 1px;
      border-bottom: 1px solid rgba(240, 232, 255, 0.1);
    }
    .burn_line {
      display: inline-block;
      margin: 30px 0 30px;
      width: 340px;
      height: 1px;
      border-bottom: 1px dashed rgba(240, 232, 255, 0.3);
    }
    .burn_title1 {
      z-index: 3;
      position: relative;
      opacity: 0;
      display: inline-block;
      font-family: Poppins;
      font-weight: 600;
      font-size: 36px;
      line-height: 54px;
      color: #fff;
      & > div {
        font-weight: 600;
        font-size: 36px;
        line-height: 54px;
      }
    }
    .burn_desc1 {
      opacity: 0;
      margin-top: 30px;
      margin-bottom: 30px;
      font-family: Poppins;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: rgba(180, 171, 219, 1);
    }
    .burn_title2 {
      opacity: 0;
      font-family: Poppins;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      color: rgba(180, 171, 219, 0.7);
    }

    .burn_num2 {
      opacity: 0;
      margin-top: 10px;
      font-family: Poppins;
      font-weight: 700;
      font-size: 30px;
      height: 45px;
      line-height: 45px;
      color: #fff;
      span {
        font-family: Poppins-Bold;
        &:first-child {
          margin-right: 10px;
        }
        &:last-child {
          font-weight: 900;
          font-size: 20px;
        }
      }
    }
    .burn_num3 {
      opacity: 0;
      margin-top: 6px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: rgba(180, 171, 219, 0.7);
    }

    .burn_btn {
      opacity: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 270px;
      height: 54px;
      border-radius: 54px;
      font-family: Poppins;
      font-weight: 600;
      font-size: 20px;
      line-height: 30px;
      color: #fff;
      cursor: pointer;
      background: rgba(100, 0, 255, 1);
      margin-top: 60px;
      span {
        margin-right: 10px;
      }
      svg {
        display: block;
        height: 16px;
        transition: all 0.3s;
      }
      &:hover,
      &:active {
        background: linear-gradient(90.2deg, #3eb5ff -10.96%, #6e0dff 80.53%);
        svg {
          transform: translateX(4px);
        }
      }
    }
  }
}

.core {
  position: absolute;
  // right: -135px;
  right: -45px;
  bottom: 0;
  width: 944px;
  height: 764px;
  .core_list {
    z-index: 3;
    position: relative;
    width: 100%;
    height: 100%;
    .core_item {
      opacity: 0;
      position: absolute;
      left: 347px;
      top: 62px;
      display: flex;
      align-items: center;
      opacity: 0;
      // padding: 19px 23px 0;
      padding: 0 23px;
      width: 465px;
      height: 118px;
      background: rgba(10, 1, 24, 0.1);
      backdrop-filter: blur(4px);
      .core_item_icon {
        margin-right: 20px;
        width: 50px;
        height: 50px;
        background: url(../../../assets/images/home2/sunToken/ico_core_1.png) no-repeat;
        background-size: 100% 100%;
      }
      .core_item_info {
        flex: 1;
        width: 0;
      }
      &:nth-child(2) {
        left: 0;
        top: 240px;
        // padding: 19px 20px 0 30px;
        padding: 0 20px 0 30px;
        width: 465px;
        .core_item_icon {
          background-image: url(../../../assets/images/home2/sunToken/ico_core_2.png);
        }
      }
      &:nth-child(3) {
        left: 227px;
        top: 422px;
        width: 478px;
        // padding: 17px 30px 0;
        padding: 0 30px;
        .core_item_icon {
          background-image: url(../../../assets/images/home2/sunToken/ico_core_3.png);
        }
      }

      .core_item_name {
        font-family: Poppins;
        font-weight: 600;
        font-size: 18px;
        line-height: 27px;
        height: 27px;
        color: #fff;
      }
      .core_item_desc {
        margin-top: 13px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 22px;
        color: rgba(180, 171, 219, 0.7);
      }
    }
  }
  .video {
    z-index: 1;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    .video_light {
      z-index: 2;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: url(../../../assets/images/home2/sunToken/sunTokenLight.png) no-repeat;
      background-size: 100% 100%;
    }
    .video_bg {
      z-index: 1;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: url(../../../assets/images/home2/sunToken/sunToken_cover.png) no-repeat;
      background-size: 100% 100%;
    }
    video {
      z-index: 1;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
    }
  }
}

.sunTokenActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}
.dataActive {
  .market {
    .market_list {
      .market_item {
        animation: homeFlipInX 0.8s ease-in-out 0.2s forwards;
      }
    }
  }
  .burn {
    .burn_title1 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_desc1 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_title2 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_num2 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_num3 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_btn {
      animation: homeFadeInUp 0.8s ease-in-out 0.6s forwards;
    }
  }
}

.coreActive {
  .core_title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .core_list {
    .core_item {
      &:nth-child(1) {
        animation: homeFadeIn 0.8s ease-in-out 0.3s forwards;
      }
      &:nth-child(2) {
        animation: homeFadeIn 0.8s ease-in-out 0.3s forwards;
      }
      &:nth-child(3) {
        animation: homeFadeIn 0.8s ease-in-out 0.3s forwards;
      }
    }
  }
}

