@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
  .sub_title {
    // margin-top: 10px;
    margin: 10px auto 0;
    width: 325px;
    font-size: 14px;
    line-height: 22px;
  }
  .sunToken {
    &::before {
      top: -30px;
      left: 0;
      width: 100%;
      height: 260px;
      transform: translateX(0);
      background-image: url(../../../assets/images/home2/sunToken/box_bg_1_m.png);
    }
    &::after {
      width: 160px;
      height: 435px;
      top: 168px;
      left: initial;
      right: 0;
      background-image: url(../../../assets/images/home2/sunToken/box_bg_2_m.png);
    }
    .sunTokenInner {
      margin: 0 auto;
      width: 100%;
    }
    .box {
      margin: 0 auto;
      width: 100%;
    }
  }
  .data {
    display: block;
    margin-top: 44px;
    .market {
      .market_title {
        top: -6px;
        span {
          font-size: 12px;
          line-height: 18px;
        }
        .market_refresh {
          span {
            margin-right: 2px;
          }
        }
      }

      .market_list {
        padding: 0 15px;
        background: transparent;
        backdrop-filter: none;
        &::before {
          display: none;
        }
        .market_item {
          position: relative;
          padding: 8px 10px 44px;
          // height: 130px;
          height: initial;
          margin-right: 8px !important;
          background: rgba(10, 1, 24, 0.3);
          border-radius: 10px;
          &::before {
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 10px;
            padding: 1px;
            background: linear-gradient(180deg, rgb(167, 162, 255, 0.3) 0%, rgba(167, 162, 255, 0) 100%);
            -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
            -webkit-mask-composite: xor; /* Safari/Chrome */
            mask-composite: exclude; /* Firefox */
            pointer-events: none;
          }
          &:last-child {
            margin-right: 0;
          }

          .item_name {
            display: block;
            span {
              font-size: 12px;
              line-height: 18px;
              margin-right: 0;
            }
            .item_rate {
              display: none;
            }
          }
          .item_rate {
            display: block;
            margin-top: 2px;
            font-size: 12px;
            line-height: 18px;
            height: 18px;
          }
          .item_num {
            font-family: Poppins;
            font-weight: 600;
            margin-top: 6px;
            margin-bottom: 2px;
            font-size: 14px;
            line-height: 24px;
          }
          .item_chart {
            top: initial;
            left: 10px;
            bottom: 10px;
            width: calc(100% - 20px);
            height: 30px;
          }
        }
      }
    }
    .burn {
      position: relative;
      margin-top: 0;
      padding: 29px 15px 30px;
      min-height: initial;
      .burn_line {
        display: block;
        margin: 20px 0;
        width: 260px;
      }
      .burn_line0 {
        display: block;
        margin: 20px 0;
        width: 100%;
      }
      .burn_title1 {
        font-size: 20px;
        line-height: 30px;
      }
      .burn_desc1 {
        margin-top: 10px;
        margin-bottom: 20px;
        font-size: 12px;
        line-height: 18px;
      }
      .burn_title2 {
        font-size: 14px;
        line-height: 20px;
      }

      .burn_num2 {
        font-size: 24px;
        line-height: 36px;
        span {
          &:first-child {
            margin-right: 6px;
          }
          &:last-child {
            font-size: 14px;
          }
        }
      }
      .burn_num3 {
        margin-top: 6px;
        font-size: 14px;
        line-height: 20px;
      }
      .burn_btn {
        width: 100%;
        height: 40px;
        border-radius: 40px;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        margin-top: 26px;
        span {
          margin-right: 10px;
        }
        svg {
          height: 14px;
        }
      }
    }
  }

  .core {
    position: relative;
    right: 0;
    bottom: 0;
    width: 100%;
    height: initial;
    .core_list {
      z-index: 3;
      position: relative;
      padding: 0 15px;
      width: 100%;
      height: initial;
      .core_item {
        position: relative;
        display: block;
        left: 0;
        top: 0;
        opacity: 0;
        padding: 10px;
        margin-bottom: 10px;
        width: 100% !important;
        height: initial;
        border-radius: 10px;
        overflow: hidden;
        background: rgba(21, 12, 40, 0.4);
        &:last-child {
          margin-bottom: 0;
        }
        &::before {
          content: '';
          position: absolute;
          inset: 0;
          border-radius: 10px;
          padding: 1px;
          background: linear-gradient(180deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.05) 100%);
          -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
          -webkit-mask-composite: xor; /* Safari/Chrome */
          mask-composite: exclude; /* Firefox */
          pointer-events: none;
        }
        .core_item_icon {
          margin-bottom: 10px;
          width: 30px;
          height: 30px;
        }
        .core_item_info {
          flex: initial;
          width: 100%;
        }
        &:nth-child(2) {
          left: 0;
          top: 0;
          padding: 10px;
          .core_item_icon {
            background-image: url(../../../assets/images/home2/sunToken/ico_core_2.png);
          }
        }
        &:nth-child(3) {
          left: 0;
          top: 0;
          width: 100%;
          padding: 10px;
          .core_item_icon {
            background-image: url(../../../assets/images/home2/sunToken/ico_core_3.png);
          }
        }

        .core_item_name {
          font-size: 16px;
          line-height: 24px;
          height: initial;
        }
        .core_item_desc {
          margin-top: 6px;
          font-size: 12px;
          line-height: 22px;
        }
      }
    }
    .video {
      display: none;
    }
  }
}

