@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.header {
  position: relative;
  z-index: 10;
  text-align: center;
}
.title {
  opacity: 0;
  font-family: Poppins;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
  color: #fff;
  text-align: center;
}
.title_end {
  color: #ffe45b;
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #fff;
  text-align: center;
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.notice {
  position: relative;
}

.notice_top {
  position: relative;
  // margin: 0 auto -100px;
  margin: -147px auto 0;
  // width: 1248px;
  width: 100%;
  height: 523px;
  background-image: url(../../../assets/images/home2/notice/bg_top_0.png);
  background-repeat: no-repeat;
  // background-size: contain;
  background-size: 100% 100%;
  pointer-events: none;
  .notice_top_2 {
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
    mask-size: 100% 100%;
    width: 100%;
    height: 100%;
    mask-repeat: no-repeat;
    transition: 1s cubic-bezier(0.6, 0.6, 0, 1) opacity;
    mask-image: url(../../../assets/images/home2/notice/bg_top_1.png);
    pointer-events: none;
  }
  .notice_top_3 {
    z-index: 3;
    position: relative;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(183, 164, 251, 0) 0, #b7a4fb 100%, #8562ff 100%, rgba(133, 98, 255, 0) 0%);
    animation: topAnimation 6s infinite cubic-bezier(0.62, 0.62, 0.28, 0.67);
    pointer-events: none;
  }
}

.notice_bottom {
  position: relative;
  height: 530px;
  top: -186px;
  width: 100%;
  margin: 0 auto;
  background-repeat: no-repeat;
  // background-size: contain;
  background-size: 100% 100%;
  pointer-events: none;

  & > * {
    pointer-events: none;
  }

  .notice_bottom_bg {
    height: 100%;
    left: 0;
    // mix-blend-mode: overlay;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 2;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .notice_bottom_cover {
    z-index: 3;
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    mask-repeat: no-repeat;
    // mask-size: contain;
    mask-size: 100% 100%;
    transition: 1s cubic-bezier(0.6, 0.6, 0, 1) opacity;
    div {
      animation: lineAnimation 4s infinite cubic-bezier(0.62, 0.62, 0.14, 1);
      // background: linear-gradient(
      //   180deg,
      //   rgba(183, 164, 251, 0) 0,
      //   #b7a4fb 100%,
      //   #8562ff 100%,
      //   rgba(133, 98, 255, 0) 0%
      // );
      background: linear-gradient(
        180deg,
        rgba(190, 130, 255, 0) 24.54%,
        rgba(190, 130, 255, 1) 53.48%,
        rgba(190, 130, 255, 1) 78.86%,
        rgba(190, 130, 255, 0) 101.66%
      );
      height: 100%;
      transform: translateY(-530px);
    }
    &:nth-child(2) div {
      animation-delay: 2s;
    }
    &:nth-child(3) div {
      animation-delay: 3s;
    }
    &:nth-child(4) div {
      animation-delay: 1s;
    }
  }
  .notice_bottom_cover_ray {
    div {
      animation: bottomRayAnimation 10.2s infinite cubic-bezier(0.62, 0.62, 0, 1);
      // background-image: linear-gradient(
      //   180deg,
      //   rgba(255, 255, 255, 0) 10.42%,
      //   rgba(255, 255, 255, 0.1) 26.56%,
      //   rgba(255, 255, 255, 0) 37.5%
      // );
      background: rgba($color: #cba2ff, $alpha: 0.7);
      height: 100%;
    }
  }
  .notice_bottom_cover_1 {
    mask-image: url(../../../assets/images/home2/notice/line1.png);
  }
  .notice_bottom_cover_2 {
    mask-image: url(../../../assets/images/home2/notice/line2.png);
  }
  .notice_bottom_cover_3 {
    mask-image: url(../../../assets/images/home2/notice/line3.png);
  }
  .notice_bottom_cover_4 {
    mask-image: url(../../../assets/images/home2/notice/line4.png);
  }
  .notice_bottom_cover_ray {
    mask-image: url(../../../assets/images/home2/notice/ray1.png);
  }
}
.notice_bottom_m {
  display: none;
}

.notice_bottom_light {
  background: url(../../../assets/images/home2/notice/hero-background-lights.png);
  height: 725px;
  left: 50%;
  position: absolute;
  bottom: -100px;
  transform: translate(-50%);
  width: 1680px;
}

.notice_content {
  margin: -60px auto 0;
  // padding: 260px 0 308px;
  width: 1440px;
}

.list {
  position: relative;
  // margin-top: 65px;
  margin-top: 50px;
  height: 410px;
  .item_wrap {
    display: flex;
  }
  .item {
    position: relative;
    opacity: 0;
    box-sizing: border-box;
    z-index: 12;
    position: relative;
    margin-right: 30px;
    flex-shrink: 0;
    width: 337px;
    height: 380px;
    border-radius: 20px;
    transition: all 0.3s;
    background: rgba(21, 12, 40, 0.4);
    backdrop-filter: blur(10px);
    overflow: hidden;
    a {
      box-sizing: border-box;
      display: block;
      width: 100%;
      height: 100%;
      padding: 20px;
      background: rgba(21, 12, 40, 0.4);
    }
    &::after {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: 20px;
      padding: 1px; /*  */
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
      -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
      -webkit-mask-composite: xor; /*  */
      mask-composite: exclude;
      pointer-events: none;
      z-index: -1;
    }
    &:last-child {
      margin-right: 0;
    }
    &:hover {
      .item_more {
        // color: #ffee99;
      }
    }
    .video {
      display: none;
      z-index: -1;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
    }
  }
  .poster {
    opacity: 0;
    height: 170px;
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    overflow: hidden;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .item_date {
    opacity: 0;
    margin: 10px 0 20px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    overflow: hidden;
    color: rgba(255, 255, 255, 0.7);
  }
  .item_title {
    opacity: 0;
    height: 63px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #fff;
    transition: all 0.3s ease-in-out;
  }
  .item_more {
    opacity: 0;
    position: absolute;
    left: 20px;
    bottom: 20px;
    color: #ffe45b;
    &:hover {
      color: #ffee99;
    }
  }

  .item_active {
    transform: translateY(-15px);
    transform: scale(1.09);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    @include webpSupport {
      background-image: url(../../../assets/images/home2/notice/bg_active_ani_new.webp);
    }
    @include webpUnSupport {
      background-image: url(../../../assets/images/home2/notice/bg_active_ani_new.png);
    }
    // width: 360px;
    // height: 410px;
    a {
      background: transparent;
      // background: url(../../../assets/images/home2/notice/bg_active.png) no-repeat;
      // background-size: 100% 100%;
    }
    .item_more {
      // color: #ffee99;
    }

    &::after {
      display: none;
      // z-index: 1;
      // background: rgba(65, 51, 134, 1);
      // background: transparent;
    }
    .poster {
      // height: 200px;
    }
    .item_title {
      // height: 72px;
      // font-size: 16px;
      // line-height: 24px;
    }
    .video {
      // display: block;
      display: none;
    }
  }
}

.more {
  opacity: 0;
  position: absolute;
  right: 10px;
  top: -47px;
  color: #fff;
}

.noticeActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.listActive {
  .more {
    animation: homeZoomIn 0.5s ease-in-out 1.2s forwards;
  }
  .item {
    animation: homeFadeIn 0.8s ease-in-out 0.4s forwards;
    .poster {
      animation: homeFadeIn 0.8s ease-in-out 0.8s forwards;
    }
    .item_title {
      animation: homeFadeInUp 0.8s ease-in-out 0.8s forwards;
    }
    .item_date {
      animation: homeFadeInUp 0.8s ease-in-out 0.8s forwards;
    }
    .item_more {
      animation: homeFadeInUp 0.8s ease-in-out 0.8s forwards;
    }
  }
}

@keyframes topAnimation {
  0% {
    transform: translateY(-523px);
  }
  33%,
  to {
    transform: translateY(523px);
  }
}

@keyframes topAnimation2 {
  0% {
    transform: translateY(-102px);
  }
  33%,
  to {
    transform: translateY(102px);
  }
}

@keyframes lineAnimation {
  0% {
    opacity: 1;
    transform: translateY(-530px);
  }

  60% {
    opacity: 1;
    transform: translateY(530px);
  }

  99% {
    opacity: 1;
    transform: translateY(530px);
  }
  100% {
    opacity: 0;
    transform: translateY(530px);
  }
}

@keyframes lineAnimationM {
  0% {
    opacity: 1;
    transform: translateY(-102px);
  }

  60% {
    opacity: 1;
    transform: translateY(102px);
  }

  99% {
    opacity: 1;
    transform: translateY(102px);
  }
  100% {
    opacity: 0;
    transform: translateY(102px);
  }
}
@keyframes bottomRayAnimation {
  0% {
    opacity: 1;
    transform: translateY(-530px);
  }
  49.01% {
    opacity: 1;
    transform: translateY(-530px);
  }

  // 68.61%,
  // 88.21% {
  //   opacity: 1;
  //   transform: translateY(0);
  // }
  60%,
  70% {
    opacity: 1;
    transform: translateY(0);
  }

  80% {
    opacity: 1;
    transform: translateY(530px);
  }

  99% {
    opacity: 0;
    transform: translateY(530px);
  }

  to {
    opacity: 0;
    transform: translateY(-530px);
  }
}

@keyframes bottomRayAnimationM {
  0% {
    opacity: 1;
    transform: translateY(-102px);
  }
  49.01% {
    opacity: 1;
    transform: translateY(-102px);
  }

  // 68.61%,
  // 88.21% {
  //   opacity: 1;
  //   transform: translateY(0);
  // }
  60%,
  70% {
    opacity: 1;
    transform: translateY(0);
  }

  80% {
    opacity: 1;
    transform: translateY(102px);
  }

  99% {
    opacity: 0;
    transform: translateY(102px);
  }

  to {
    opacity: 0;
    transform: translateY(-102px);
  }
}

@include pc-small-screen {
  .notice_content {
    width: 1200px;
    .list {
      .item {
        width: 280px;
        height: 350px;
      }
      .poster {
        height: 140px;
      }
    }
  }
  .list {
    .item_active {
      // background-image: url(../../../assets/images/home2/notice/bg_active_ani_new.png);
      @include webpSupport {
        background-image: url(../../../assets/images/home2/notice/bg_active_ani_new_small.webp);
      }
      @include webpUnSupport {
        background-image: url(../../../assets/images/home2/notice/bg_active_ani_new.png);
      }
    }
  }
  .notice_bottom {
    top: -217px;
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
  .sub_title {
    margin: 10px auto 0;
    width: 325px;
    font-size: 14px;
    line-height: 22px;
  }
  .notice_top {
    // margin-top: -29px;
    margin-top: 0;
    width: 100%;
    height: 102px;
    .notice_top_3 {
      animation: topAnimation2 6s infinite cubic-bezier(0.62, 0.62, 0.28, 0.67);
    }
  }
  .notice_bottom {
    // display: none;
    top: -25px;
    height: 102px;
    .notice_bottom_cover {
      div {
        animation: lineAnimationM 4s infinite cubic-bezier(0.62, 0.62, 0.14, 1);
      }
    }
    .notice_bottom_cover_ray {
      div {
        animation: bottomRayAnimationM 10.2s infinite cubic-bezier(0.62, 0.62, 0, 1);
      }
    }
  }
  .notice_bottom_m {
    margin-top: -25px;
    margin-bottom: -17px;
    display: block;
    width: 100%;
    height: 102px;
    background: url(../../../assets/images/home2/notice/notice_bottom_m.png) no-repeat;
    background-size: 100% 100%;
    pointer-events: none;
  }

  .notice_content {
    width: 100%;
    margin-top: -40px;
  }
  .list {
    margin-top: 42px;
    height: initial;
    .item_wrap {
      display: block;
      padding: 0 15px;
    }
    .item {
      margin-right: 0;
      margin-bottom: 10px;
      width: 100%;
      height: initial;
      border-radius: 10px;
      a {
        background: transparent;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &::after {
        border-radius: 10px;
      }
      &:hover,
      &:active {
        background: url(../../../assets/images/home2/notice/bg_active_m.png) no-repeat !important;
        background-size: 100% 100% !important;
        &::after {
          display: block !important;
          background: #413386;
        }
      }
      .video {
        display: none !important;
      }
      a {
        position: relative;
        display: flex;
        padding: 10px;
      }
      .poster {
        margin-right: 10px;
        width: 140px;
        height: 94px;
        border: 0;
        border-radius: 10px;
      }
    }
    .item_active {
      .video {
        display: none !important;
      }
    }
    .item_more {
      display: none;
    }
    .item_title {
      flex: 1;
      width: 0;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
      text-overflow: ellipsis;
      font-family: Poppins;
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      height: 66px;
    }
    .item_date {
      position: absolute;
      left: 160px;
      bottom: 10px;
      margin: 0;
      font-family: Poppins;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      text-align: left;
    }
    .item_active {
      background: rgba(21, 12, 40, 0.4) !important;
      transform: translateY(0);
      &::after {
        display: block !important;
      }
      .item_title {
        height: 66px;
        font-size: 14px;
        line-height: 22px;
      }
    }
  }
  .more {
    top: -32px;
    right: 25px;
    z-index: 13;
  }
}
