@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.header {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
  text-align: center;
  color: #fff;
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  padding: 0 10px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  color: #fff;
}
.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.develop {
  .list_wrap {
    z-index: 12;
    position: relative;
    width: 1440px;
    margin: 50px auto 0;
    .list {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    .item {
      opacity: 0;
      position: relative;
      height: 240px;
      background: url('../../../assets/images/home2/develop/bg_develop.png') no-repeat;
      background-size: 100% 100%;
      cursor: pointer;
      &:hover {
        background-image: url('../../../assets/images/home2/develop/bg_develop_active.png');
        .item_icon {
          opacity: 1;
        }
      }
      .item_icon {
        position: absolute;
        left: 40px;
        top: 30px;
        width: 40px;
        height: 40px;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        opacity: 1;
      }
      .item_card {
        z-index: 1;
        position: absolute;
        right: 40px;
        top: 30px;
        width: 230px;
        height: 178px;
      }
      .item_title {
        opacity: 0;
        z-index: 2;
        position: absolute;
        left: 40px;
        bottom: 82px;
        font-family: Poppins;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        color: #fff;
        & > div {
          font-weight: 600;
          font-size: 24px;
          line-height: 36px;
        }
      }
      .item_desc {
        opacity: 0;
        z-index: 3;
        position: absolute;
        left: 40px;
        bottom: 40px;
        font-family: Poppins;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: rgba(180, 171, 219, 1);
      }
    }
  }
  .list_wrap {
    .list {
      &:nth-child(1) {
        .item {
          &:nth-child(1) {
            .item_icon {
              background-image: url('../../../assets/images/home2/develop/card1.png');
            }
            .item_card_icon1 {
              z-index: 1;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home2/develop/g1_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 2;
              position: absolute;
              left: 12px;
              top: 8px;
              width: 90px;
              height: 90px;
              background-image: url('../../../assets/images/home2/develop/g1_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              right: 36px;
              top: 95px;
              width: 33px;
              height: 21px;
              background-image: url('../../../assets/images/home2/develop/g1_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            &:hover {
              .item_card_icon2 {
                left: 28px;
              }
              .item_card_icon3 {
                right: 50px;
              }
            }
          }
          &:nth-child(2) {
            .item_icon {
              background-image: url('../../../assets/images/home2/develop/card2.png');
            }
            .item_card_icon1 {
              z-index: 2;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home2/develop/g2_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 1;
              position: absolute;
              left: 3px;
              top: 39px;
              width: 76px;
              height: 52px;
              background-image: url('../../../assets/images/home2/develop/g2_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              right: 18px;
              top: 66px;
              width: 46px;
              height: 46px;
              background-image: url('../../../assets/images/home2/develop/g2_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon4 {
              z-index: 4;
              position: absolute;
              left: 46px;
              bottom: 22px;
              width: 80px;
              height: 66px;
              background-image: url('../../../assets/images/home2/develop/g2_4.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            &:hover {
              .item_card_icon2 {
                left: 15px;
              }
              .item_card_icon3 {
                right: 28px;
              }
              .item_card_icon4 {
                left: 36px;
              }
            }
          }
        }
      }
      &:nth-child(2) {
        .item {
          &:nth-child(1) {
            .item_icon {
              background-image: url('../../../assets/images/home2/develop/card3.png');
            }
            .item_card_icon1 {
              z-index: 2;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home2/develop/g3_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 3;
              position: absolute;
              left: 60px;
              bottom: 57px;
              width: 52px;
              height: 52px;
              background-image: url('../../../assets/images/home2/develop/g3_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              right: 27px;
              bottom: 56px;
              width: 46px;
              height: 46px;
              background-image: url('../../../assets/images/home2/develop/g3_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }

            &:hover {
              .item_card_icon2 {
                left: 59px;
                bottom: 57px;
                width: 64px;
                height: 64px;
              }
              .item_card_icon3 {
                right: 37px;
                bottom: 56px;
              }
            }
          }
          &:nth-child(2) {
            .item_icon {
              background-image: url('../../../assets/images/home2/develop/card4.png');
            }
            .item_card_icon1 {
              z-index: 1;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home2/develop/g4_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 2;
              position: absolute;
              left: 0;
              top: 62px;
              width: 41px;
              height: 26px;
              background-image: url('../../../assets/images/home2/develop/g4_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              left: 49px;
              top: 100px;
              width: 64px;
              height: 44px;
              background-image: url('../../../assets/images/home2/develop/g4_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon4 {
              z-index: 4;
              position: absolute;
              right: 6px;
              top: 85px;
              width: 60px;
              height: 34px;
              background-image: url('../../../assets/images/home2/develop/g4_4.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            &:hover {
              .item_card_icon2 {
                left: 16px;
              }
              .item_card_icon3 {
                left: 39px;
              }
              .item_card_icon4 {
                right: 16px;
              }
            }
          }
        }
      }
    }
  }
}

.developActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.develop {
  .listActive {
    .item {
      animation: homeFadeIn 0.8s ease-in-out 0s forwards;
      .item_title {
        animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
      }
      .item_desc {
        animation: homeFadeInUp 0.8s ease-in-out 1.2s forwards;
      }
    }
  }
}

@include pc-small-screen {
  .develop {
    .list_wrap {
      width: 1200px;
      .list {
        margin-bottom: 20px;
        gap: 20px;
        &:last-child {
          margin-bottom: 0;
        }
        .item {
          .item_icon {
            left: 30px;
          }
          .item_title {
            left: 30px;
          }
          .item_desc {
            left: 30px;
          }
          .item_card {
            right: 30px;
          }
        }
      }
    }
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
  .sub_title {
    margin: 10px auto 0;
    width: 325px;
    font-size: 14px;
    line-height: 21px;
  }

  .develop {
    .list_wrap {
      margin-top: 20px;
      width: 100%;
      padding: 0 15px;
      .list {
        grid-template-columns: 1fr;
        gap: 10px;
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        &:nth-child(1) {
          .item {
            height: 178px;
            animation: none;
            opacity: 1;
            .item_icon {
              width: 30px;
              height: 30px;
              left: 20px;
              top: 20px;
            }
            .item_card {
              right: 20px;
              bottom: initial;
              top: 10px;
              width: 142px;
              height: 110px;
            }
            .item_title {
              left: 20px;
              bottom: 51px;
              font-size: 18px;
              line-height: 27px;
              & > div {
                font-size: 18px;
                line-height: 27px;
              }
            }
            .item_desc {
              font-size: 14px;
              line-height: 21px;
              left: 20px;
              bottom: 20px;
            }
            .item_card_icon2,
            .item_card_icon3,
            .item_card_icon4 {
              display: none;
            }
            &:nth-child(1) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home2/develop/bg_m_1.png');
              }
            }
            &:nth-child(2) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home2/develop/bg_m_2.png');
              }
            }
          }
        }
        &:nth-child(2) {
          .item {
            height: 178px;
            animation: none;
            opacity: 1;
            .item_icon {
              width: 30px;
              height: 30px;
              left: 20px;
              top: 20px;
            }
            .item_card {
              right: 20px;
              bottom: initial;
              top: 10px;
              width: 142px;
              height: 110px;
            }
            .item_title {
              left: 20px;
              bottom: 51px;
              font-size: 18px;
              line-height: 27px;
              & > div {
                font-size: 18px;
                line-height: 27px;
              }
            }
            .item_desc {
              font-size: 14px;
              line-height: 21px;
              left: 20px;
              bottom: 20px;
            }
            .item_card_icon2,
            .item_card_icon3,
            .item_card_icon4 {
              display: none;
            }
            &:nth-child(1) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home2/develop/bg_m_3.png');
              }
            }
            &:nth-child(2) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home2/develop/bg_m_4.png');
              }
            }
          }
        }
        .item {
          position: relative;
          // background: rgba(21, 12, 40, 0.4);
          background: #0e051e;
          border-radius: 10px;
          &:before {
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 10px;
            padding: 1px;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.05) 100%);
            -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
            -webkit-mask-composite: xor; /* Safari/Chrome */
            mask-composite: exclude; /* Firefox */
            pointer-events: none;
          }
          &:hover {
            background: url(../../../assets/images/home2/develop/bg_develop_active_m.png) no-repeat;
            background-repeat: no-repeat;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
}
