@import "../../../assets/css/mixins";

.star_container {
  position: absolute;
  background: transparent;
  overflow: hidden;
  top: 0;
  height: 320px;
  width: 1440px;
  left: 50%;
  margin-left: -720px;
}

.star {
  position: absolute;
  bottom: 0;
  background: radial-gradient(
                  circle,
                  rgba(255, 255, 200, 1) 0%,
                  rgba(255, 255, 200, 0.3) 70%,
                  rgba(255, 255, 200, 0) 100%
  );
  border-radius: 50%;
  pointer-events: none;
  animation: floatUp linear forwards;
  backdrop-filter: blur(2px)
}

@include pc-small-screen {
  .star_container {
    width: 1200px;
    margin-left: -600px;
  }
}

@keyframes floatUp {
  0% {
    transform: translateY(0);
    opacity: 0;
  }
  33% {
    transform: translateY(-100px);
    opacity: 1;
  }
  66% {
    transform: translateY(-200px);
    opacity: 1;
  }
  100% {
    transform: translateY(-300px);
    opacity: 0;
  }
}
