@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.header {
  z-index: 12;
  position: relative;
  text-align: center;
}

.title {
  opacity: 0;
  font-family: Poppins;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
  color: #fff;
  text-align: center;
}
.title_end {
  color: #ffe45b;
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #fff;
  text-align: center;
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.partners {
  width: 100%;
  overflow: hidden;
  .main_wrap {
    margin-top: 50px;
  }
  .list {
    display: flex;
    height: 140px;
    margin-top: 10px;
    &:hover {
      .list_first,
      .list_second {
        animation-play-state: paused !important;
      }
    }
    .list_first {
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      align-items: center;
      animation: marquee-loop 100s -50s linear infinite;
      height: 120px;
    }
    .list_second {
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      align-items: center;
      animation: marquee-loop2 100s linear infinite;
      height: 120px;
    }
    &:nth-child(2) {
      .list_first {
        animation: marquee-loop 116s -58s linear infinite;
      }
      .list_second {
        animation: marquee-loop2 116s linear infinite;
      }
    }
  }

  .item {
    display: flex;
    align-items: center;
    box-sizing: content-box;
    // min-width: 320px;
    height: 80px;
    margin: 0 15px;
    padding: 20px 30px;
    // background-repeat: no-repeat;
    // background-size: 100% 100%;
    color: #fff;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    cursor: pointer;
    position: relative;
    transform-origin: center;
    background: rgba(21, 12, 40, 0.4);
    backdrop-filter: blur(10px);
    overflow: hidden;
    border-radius: 20px;

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: 20px;
      padding: 1px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
      -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
      -webkit-mask-composite: xor; /* Safari/Chrome */
      mask-composite: exclude; /* Firefox */
      pointer-events: none;
    }

    &:hover {
      // min-width: 340px;
      height: 80px;
      padding: 30px 40px;
      // transform: scale(1.16);
      margin-left: 5px; // 15 - 10 = 5 (10)
      margin-right: 5px;
      background: url(../../../assets/images/home2/parnters/bg_active.png) no-repeat;
      background-size: 100% 100%;
      &::before {
        background: rgba(65, 51, 134, 1);
      }
    }
    .logo {
      flex-shrink: 0;
      width: 50px;
      height: 50px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .info {
      margin-left: 20px;
      .info_title {
        font-family: Poppins;
        font-weight: 500;
        font-size: 20px;
        line-height: 30px;
        color: #fff;
      }
      .info_desc {
        margin-top: 6px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: rgba(255, 255, 255, 0.7);
      }
    }
  }
  .main_pc {
    display: block;
    height: 290px;
  }
  .list_wrap {
    opacity: 0;
  }
  .main_mobile {
    display: none;
  }
}

.partnerActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.mainPcActive {
  .list_wrap {
    animation: homeFadeInBottomRight2 0.8s ease-in-out 0.4s forwards;
  }
}

@keyframes marquee-loop {
  0% {
    transform: translate(100%);
  }

  to {
    transform: translate(-100%);
  }
}

@keyframes marquee-loop2 {
  0% {
    transform: translate(0);
  }

  to {
    transform: translate(-200%);
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
  .title_end {
    color: #ffe45b;
  }
  .sub_title {
    margin: 10px auto 0;
    width: 325px;
    font-size: 14px;
    line-height: 22px;
  }

  .partners {
    padding: 0 15px;
    .main_wrap {
      margin-top: 20px;
    }
    .list {
      display: block;
      height: initial;
      .list_first {
        display: block;
        height: initial;
        animation: none;
      }
      .list_second {
        display: none;
      }
    }
    .item {
      padding: 12px 10px;
      margin: 0 0 10px 0;
      height: initial;
      // border: 1px solid rgba(255, 255, 255, 0.15);
      border-radius: 10px;
      background: url(../../../assets/images/home2/parnters/bg_m.png);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      &::after {
        content: '';
        position: absolute;
        inset: 0;
        border-radius: 10px;
        padding: 1px; /*  */
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
        -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
        -webkit-mask-composite: xor; /*  */
        mask-composite: exclude;
        pointer-events: none;
        z-index: -1;
      }
      // &:last-child {
      //   margin-bottom: 0;
      // }
      &:hover,
      &:active {
        width: 100%;
        min-width: 100%;
        height: inherit;
        padding: 12px 10px;
        margin-left: 0;
        margin-right: 0;
        background-image: url(../../../assets/images/home2/parnters/bg_active_m.png);
      }
      .info {
        margin-left: 10px;
        .info_title {
          font-weight: 500;
          font-size: 16px;
          line-height: 24px;
        }
        .info_desc {
          margin-top: 6px;
          font-family: Poppins;
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
        }
      }
    }
    .main_pc {
      display: none;
    }
    .main_mobile {
      display: block;
      .item {
        box-sizing: border-box;
        &::before {
          display: none;
        }
      }
    }
    .more_wrap {
      display: block;
      margin-top: 20px;
      text-align: center;
      .more {
        display: inline-flex;
        align-items: center;
        cursor: pointer;
        span {
          margin-right: 6px;
          font-family: Poppins;
          font-weight: 400;
          font-size: 14px;
          line-height: 21px;
          color: rgba(191, 171, 255, 1);
        }
        img {
          display: block;
          width: 12px;
          height: 12px;
          transition: all 0.3s;
        }
      }
      .more_active {
        img {
          transform: rotate(180deg);
        }
      }
    }
  }
  .mainActive {
    animation: none;
  }
}
