@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -66px;
        top: 0;
        width: 46px;
        height: 32px;
        background: url(../../../assets/images/home/base/ico_pools_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.pools {
  .pc_container {
    // opacity: 0;
  }
  .list {
    margin-top: 50px;
  }
  .card {
    position: relative;
    width: 300px;
    height: 420px;
    //background: url(../../../assets/images/home2/pools/card_bg.svg) no-repeat;
    //background-position: 0 0;
    //background-size: 100% 100%;
    padding: 30px 20px 20px;
    overflow: hidden;
    //border-radius: 20px;
    transition: all 0.3s ease;
    @include gradient-glass-border(linear-gradient(180deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.05) 100%), 1px, 20px, #150C2866, 10px);
    @include pc-only {
      &:hover {
        background-image: url(../../../assets/images/home2/pools/bg_active.png);
        background-position: 0 0;
        background-size: 100% 100%;
        transform: scale(1.06);
        &::after, &::before {
          display: none;
        }
      }
    }
  }
  .card_wrap {
    height: 448px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 330px;
  }
  .tag {
    position: absolute;
    left: 0;
    top: 0;
    height: 24px;
    //padding: 0 20px;
    //border-radius: 0 0 20px 0;
    font-family: Poppins;
    font-weight: 600;
    font-size: 12px;
    line-height: 24px;
    color: rgba(195, 177, 255, 1);
    //background: rgba(213, 171, 255, 0.2);
    text-align: center;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center;
    &_mini {
      width: 36px;
      background-image: url(../../../assets/images/home2/pools/tag_mini.svg);
    }
    &_big {
      width: 59px;
      background-image: url(../../../assets/images/home2/pools/tag_big.svg);
    }
  }

  .name {
    font-family: Poppins;
    font-weight: 600;
    font-size: 20px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
  }
  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    .icon_img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: #fff;
      overflow: hidden;
      &:last-child {
        margin-left: -12px;
      }
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  .tvl {
    margin-top: 10px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 14px;
    height: 21px;
    line-height: 21px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex;
    justify-content: center;
  }
  .tvl_label {
    margin-right: 8px;
    color: #b4abdb;
  }
  .tvl_num {
    color: #fff;
  }
  .apy {
    //display: flex;
    //justify-content: space-between;
    //border-top: 1px dashed #F0E8FF;
    margin-top: 30px;
    //height: 48px;
    text-align: center;
  }
  .dashed {
    height: 1px;
    margin-top: 30px;
    background: url('../../../assets/images/home2/pools/dashed.png') repeat-x;
    background-size: 100% 100%;
    @include mobile-only {
      background-image: url('../../../assets/images/home2/pools/dashed_m.png');
    }
  }

  .apy_label {
    position: relative;
    font-family: Poppins;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #b4abdb;
    margin-bottom: 10px;
  }
  .apy_value {
    font-family: Poppins-Bold;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;
    color: rgba(0, 222, 171, 1);
  }
  .tip {
    margin-top: 20px;
    font-family: Poppins;
    font-weight: 500;
    font-size: 12px;
    height: 18px;
    line-height: 18px;
    color: #b4abdb;
    text-align: center;
  }
  .btn {
    width: 100%;
    margin: 12px auto 0;
    background: rgba(#ae9aff, 0.15);
    border: none;
    border-radius: 30px;
    color: #bfabff;
    height: 40px;
    line-height: 40px;
    //font-weight: 500;
    @include pc-only {
      &:hover {
        background: #ae9aff;
        color: #280065;
      }
    }
    @include mobile-only {
      height: 34px;
      line-height: 34px;
      &:active {
        background: #ae9aff;
        color: #280065;
      }
    }
  }
  .m_container {
    .card {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .more_wrap {
    margin-top: 20px;
    text-align: center;
    .more {
      display: inline-flex;
      align-items: center;
      span {
        margin-right: 6px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: rgba(191, 171, 255, 1);
      }
      img {
        display: block;
        width: 12px;
        height: 12px;
        transition: all 0.3s;
      }
    }
    .more_active {
      img {
        transform: rotate(180deg);
      }
    }
  }
}

.poolsActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
  // .pc_container {
  //   animation: homeFadeInBottomRight2 0.8s ease-in-out 1s forwards;
  // }
}

.marque_container {
  //opacity: 0;
}
.pc_container_active {
  .marque_container {
    animation: poolsFadeInBottomRight 0.6s ease-in-out forwards;
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -40px;
          top: -22px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin: 10px auto 0;
    width: 331px;
    font-size: 14px;
    line-height: 21px;
  }
  .pools {
    .list {
      margin-top: 20px;
      height: initial;
    }
    .card {
      margin-right: 0;
      padding: 16px 20px 20px;
      width: 100%;
      height: initial;
      border-radius: 10px;
      overflow: hidden;
      @include gradient-glass-border(linear-gradient(180deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.05) 100%), 1px, 10px, #150C2866, 10px);

    }
    .tag {
      right: 0;
      top: 0;
      left: initial;
      position: absolute;
      &_mini {
        width: 36px;
        background-image: url(../../../assets/images/home2/pools/tag_mini_m.svg);
      }
      &_big {
        width: 59px;
        background-image: url(../../../assets/images/home2/pools/tag_big_m.svg);
      }
    }
    .name {
      font-size: 16px;
      height: 24px;
      line-height: 24px;
      text-align: left;
      font-weight: 600;
    }
    .top_title {
      margin-left: 80px;
    }
    .icon {
      top: 20px;
      left: 20px;
      position: absolute;
      .icon_img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        //&:last-child {
        //  position: absolute;
        //  left: 24px;
        //  top: 24px;
        //}
      }
    }
    .dashed {
      margin-top: 16px;
    }
    .tvl {
      white-space: wrap;
      height: initial;
      word-break: break-all;
      display: flex;
      padding-left: 0;
      margin-top: 4px;
      justify-content: flex-start;
    }
    .apy {
      margin-top: 16px;
      height: 36px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .apy_label {
      line-height: 20px;
      margin-bottom: 0;
    }
    .apy_value {
      font-size: 20px;
      line-height: 30px;
    }
    .tip {
      margin-top: 14px;
    }
    .btn {
      margin-top: 10px;
      height: 34px;
      line-height: 34px;
      font-size: 14px;
    }
  }
}

